import React, { useState } from 'react';

const AddCommentForm = ({articleName, setArticleInfo}) => {

    const [userName, setUserName] = useState('');
    const [commentText, setCommentText] = useState('');
    const [messageConfirmation, setMessageConfirmation] = useState('');
    const [messageConfirmationClass, setMessageConfirmationClass] = useState('hide-me');

    const addComment = async () => {
        await fetch(
            `/api/articles/${articleName}/add-comment`,
            {
                method: 'post',
                body: JSON.stringify({userName: userName, text: commentText}),
                headers: {
                    'Content-Type': 'application/json',
                }
            }
        )
        .then(response => response.json())
        .then(body => {
            setArticleInfo(body);
            setUserName('');
            setCommentText('');
            setMessageConfirmation('Thanks for adding a comment!');
            setMessageConfirmationClass('');
        })
    }

    return (
        <div id='add-comment-form'>
            <h3>Add a Comment!</h3>
            <label>
                Name:
                <input type='text' value={userName} onChange={(event) => setUserName(event.target.value)}/>
            </label>
            <label>
                Comment:
                <textarea rows='4' cols='50' value={commentText} onChange={(event) => setCommentText(event.target.value)}/>
            </label>
            <p>Optional markup in your message:
                <ul style={{listStyle: 'none'}}>
                    <li> **bold** =&gt; <strong>bold</strong> </li>
                    <li> *italic* =&gt; <em>italic</em> </li>
                    <li> `code` =&gt; <code>code</code> </li>
                </ul>
            </p><br/>
            <button onClick={() => addComment()}>Add Comment</button>
            <div style={{padding: '10px', backgroundColor: 'rgba(0,175,85,0.3)'}} className={messageConfirmationClass}>
                {messageConfirmation}
            </div>
        </div>
    )
}


export default AddCommentForm;