import React from 'react';

const commentFormatting = (text) => {

    const markupArray = ['***','**','*','`'];
    const htmlTagArray = [['strong','em'],['strong'],['em'],['code']];
    
    const findAndReplace = (args) => {
        let boldOn    = false;
        let italicsOn = false;
        let startText = text;
        let newText   = '';
        startText.split(`${args.markup}`).forEach((arrStr) => {
            if(!boldOn && !italicsOn){
                newText += arrStr;
                if (arrStr.substring(arrStr.length - 1) === "'" || arrStr.substring(arrStr.length - 1) === "\"") {
                    newText += `${args.markup}`;
                } else {
                    boldOn = !boldOn;
                    italicsOn = !italicsOn
                }
            } else {
                newText += `<${args.htmlTag[0]}>${args.htmlTag[1] ? '<' + args.htmlTag[1] + '>' : ''}` + arrStr + `${args.htmlTag[1] ? '</' + args.htmlTag[1] + '>' : ''}</${args.htmlTag[0]}>`;
                boldOn = !boldOn
                italicsOn = !italicsOn
            }
        })
        text = newText;
        return newText;
    }
    const formatted = () => {
        let results = '';
        markupArray.forEach((markup, i) => {
            results = findAndReplace({markup: markup, htmlTag: htmlTagArray[i]});
        })
        return results;
    }
    let newText = formatted().split('\n').map((string, i) => {
        // redundant/extra newlines will be removed
        if(!string) {
            return null
        } else {
            return  <React.Fragment key={i}><p dangerouslySetInnerHTML={ {__html: string } } /><br/></React.Fragment>
        }
    });
    return newText;
}

export {
    commentFormatting,
};
