import React from 'react';
import { commentFormatting, } from '../js/myCustomJs.js'


const ArticleComments = ({article}) => (
    <>
        <h4>
            Comments:
        </h4>
        { article.comments.map((comment, i) => (
            <div className='comment' key={i}>

                    { commentFormatting(comment.text) }

                <p  style={{textAlign: 'right !important', width: '100%'}}>
                    <i><strong>- { comment.userName }</strong></i>
                </p>
            </div>
        )) }
    </>
)

export default ArticleComments