import React from 'react';

const UpvoteElement = ({articleName, upvotes, setArticleInfo}) => {

    const postUpvote = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvote`,{
            method: 'post',
        });
        const body = await result.json();
        setArticleInfo(body);
    }

    return (
        <>
            <div id='upvotes-section' className=''>
                <button onClick={() => postUpvote()} className=''>
                    Add Upvote!
                </button>
                <p>This post has been upvoted {upvotes} time{upvotes === 1 ? '' : 's'}.</p>
            </div>

        </>
    )
}

export default UpvoteElement;