import React, { useState, useEffect } from 'react';

// 404 Page
import NotFoundPage from './NotFoundPage';

// React Componants & HTML Elements
import ArticleList from '../elements/ArticleList';
import ArticleComments from '../elements/ArticleComments';
import UpvoteElement from '../elements/UpvoteElement';
import AddCommentForm from '../elements/AddCommentForm';


// Dummy Article Content
// import articleContent from './article-content';


const ArticlePage = ({ match }) => {

    const name = match.params.name;
    console.log(name);
    // const article = articleContent.find( article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments: [], title: '', content: []});
    const [allArticles, setAllArticles] = useState([]);
    

    useEffect(() => {
        const fetchArticleData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();

            setArticleInfo(body);
        };
        fetchArticleData();
        const fetchAllArticles = async () => {
            const result = await fetch(`/api/articles/`);
            const body = await result.json();

            setAllArticles(body);
        };
        fetchAllArticles();

    }, [name]);

    if (!articleInfo){ return <NotFoundPage /> }

    const otherArticles = (allArticles.filter(article => article.name !== name));


    return (
        <>
            <span id='topOfArticle'></span>
            <h1>Re: { articleInfo.title } </h1>
            <UpvoteElement articleInfo={articleInfo} articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            <hr />
            <div>
                { articleInfo.content.map((paragraph, i) => (
                    <p key={i}>{paragraph}</p>
                ))}
            </div>

            <div>
                {articleInfo.comments.length > 0 ? <ArticleComments article={ articleInfo } /> : ''}
            </div>
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />

            <h2 style={{borderRadius: '50px', marginTop: '30px', padding: '20px 0 20px 20px', boxShadow: '0 -7px 0 -0.1em rgba(0,0,0,0.2), 0 7px 0 -0.1em rgba(0,0,0,0.2), 0 0 0 6px rgba(255,255,255,1), -10px 0 0 rgba(0,0,0,0.4), 10px 0 0 rgba(0,0,0,0.4)'}}>Other Articles</h2>
            <ArticleList articles={ otherArticles } />
            
        </>
    );
}

export default ArticlePage